import { Injectable } from '@angular/core';
import { HttpClient, HttpErrorResponse } from '@angular/common/http';
import { Observable, throwError } from 'rxjs';
import { catchError } from 'rxjs/operators';

import { GlobalVariable } from '../server';
import { JsonPipe } from '@angular/common';

@Injectable({
  providedIn: 'root'
})
export class UserService {
  private url = GlobalVariable.BASE_API_URL;
  public user_account_type;
  public authId;
  
  constructor(private http: HttpClient) { 
    
  }

  public getUserDetails(): Observable<any> {
    var user = localStorage.getItem('loginSession');
    var user_id = JSON.parse(user)['pk'];
    this.authId = user_id;
    console.log(user_id);
    this.user_account_type = JSON.parse(user)['account_type'];
    return this.http.get<any>(this.url.concat('api/user/get-user-details/'+user_id, ),  {
      withCredentials: true
    }).pipe(
      catchError(this.handleError)
    );
  }

  public getUserById(id): Observable<any> {
    return this.http.get<any>(this.url.concat('api/user/get-user-details/'+id, ),  {
      withCredentials: true
    }).pipe(
      catchError(this.handleError)
    );
  }

  public getTransaction(): Observable<any> {
    var user = localStorage.getItem('loginSession');
    var user_id = JSON.parse(user)['pk'];
    this.authId = user_id;
    return this.http.get<any>(this.url.concat('api/user/get-transactions/'+user_id, ),  {
      withCredentials: true
    }).pipe(
      catchError(this.handleError)
    );
  }

  public updateProfile(pk:number,logo:string, name:string, email:string, contact_number:string, address:string, driver_route:JSON): Observable<any>{
    return this.http.put<any>(this.url.concat('api/user/update-profile'),  {
      pk_id: pk,
      logo: logo,
      name: name,
      email: email,
      contact_number: contact_number,
      address: address,
      driver_route: driver_route
   })
   .pipe(
    catchError(this.handleError)
    );
  }

  private handleError(err: HttpErrorResponse) {
    let errorMessage = '';
    if (err.error instanceof ErrorEvent) {
      errorMessage = `An error occurred: ${err.error.message}`;
    } else {
      errorMessage = `Server returned code: ${err.status}, error message is: ${err.message}`;
    }
    console.error(errorMessage);
    return throwError(errorMessage);
  }
}
